<template>
  <div>
    <i class="el-icon-close" @click="$emit('close')"></i>
    <h1>{{ scene.name }}</h1>
    <h2>Contact</h2>
    <div v-if="scene.contactPoint">
      <div v-if="scene.contactPoint">twitter: {{ contactPoint.twitter }}</div>
    </div>
    <h2>Games</h2>
    <div v-if="scene.games">
      <div v-for="game in games" :key="game.name"> {{ game.name }} </div>
    </div>
    <h2>Game Activity</h2>
    <graph :chartData="graphdata"></graph>
  </div>
</template>
<script>
import graph from './graph'
import request from 'superagent'
import consts from '../consts'
export default {
  name: 'scene',
  data () {
    return {
      graphdata: {
      },
      labels: []
    }
  },
  methods: {
    gameGraph () {
      var vm = this
      request(`${process.env.api}/graph`)
        .then((resp) => {
          console.log(resp.body)
          var labels = resp.body.reduce((acc, data) => {
            if (!acc.includes(data.Date)) {
              acc.push(data.Date)
            }
            return acc
          }, [])
          console.log(labels)
          var games = {}
          resp.body.map((data) => {
            games[data.Name] = {label: data.Name, data: []}
          })
          resp.body.map((data) => {
            games[data.Name].data.push(data.Value)
          })
          var datasets = Object.values(games).map((game, index) => {
            game['backgroundColor'] = consts.graphColours[index]
            return game
          })
          vm.graphdata = {
            labels,
            datasets
          }
        })
    }
  },
  mounted () {
    this.gameGraph()
  },
  props: ['scene'],
  components: { graph },
  computed: {
    contactPoint () {
      return JSON.parse(this.scene.contactPoint)
    },
    games () {
      return JSON.parse(this.scene.games)
    }
  }
}
</script>
<style>
.el-icon-close {
  cursor: pointer;
}
</style>



